@tailwind base;
@tailwind components;
@tailwind utilities;


/* Estilos para la tabla */
.ProductTable {
  width: 100%;
  border-collapse: collapse;
  background-color: rgb(226, 240, 255);
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Estilos para las celdas de encabezado */
.TableHeader {
  background-color: #3f9cff;
  color: #fff;
  padding: 10px;
  text-align: left;
  margin-inline: 4px;
}

/* Estilos para las filas pares */
.ProductTable tbody tr:nth-child(even) {
  background-color: rgb(236, 255, 252);
}

/* Estilos para las celdas de datos */
.ProductTable td {
  padding: 10px;
}

.Productos-Inventario {
  height: 45vh;
  overflow-y: auto;
  padding: 30px;
  border-width: 6px;
  border-color: #ffffff;
  border-radius: 30px;
  padding: 20px;
}

.Productos-Inventario2 {
  height: 70vh;
  overflow-y: auto;
  padding: 30px;
  padding: 20px;
  margin-bottom: 20px;
}

.inventario-total {
  width: 100%;
  border: 8px solid orange;
  padding: 10px;
  margin: 10px;
  border-radius: 20px;
  text-align: center;
}

.total-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.total {
  color: #4285F4;
  margin: 5px;
  font-weight: bold; /* Color verde para el valor total */
}

.ultimo{
  color: #c82333;
}

.centro{
  display: flex;
}

.boton-crear {
  height: 30px;
  border: none;
  background-color: white;
}
